@import "variables/colors";

$menu-item-background-color-active: #555;
$menu-item-border-color: #666;
$menu-item-border-color-active: #888;
$menu-dismiss-bar-hover-color: #333;
$menu-dismiss-icon-color: #aaa;

.side-menu {
  position: fixed;
  width: 420px;
  top: 0;
  bottom: 0;
  left: 0;
  background-color: $header-bg-color;
  color: $light-grey-1;
  overflow: auto;
  z-index: 2000;
  transition: left 0.4s ease-in-out, right 0.4s ease-in-out;

  &.menu-anchor-left {
    left: -100%;
    box-shadow: 1.5px 0 1.5px $header-box-shadow-color;

    &.active {
      left: 0;
    }
  }

  &.menu-anchor-right {
    right: -100%;
    left: auto;
    box-shadow: 0 1.5px 1.5px $header-box-shadow-color;

    &.active {
      right: 0;
    }
  }

  .socrata-icon {
    width: 15px;
    height: 15px;
    color: $white;

    svg {
      width: 15px;
      height: 15px;
    }
  }

  .menu-header {
    margin-top: 40px;

    .menu-header-title {
      color: $white;
      font-weight: lighter;
      text-align: center;
      padding: 20px 0 1px 0;
    }

    .menu-header-dismiss {
      position: absolute;
      top: 0;
      right: 0;
      padding: 8px 12px 6px 12px;

      .socrata-icon {
        float: right;
        color: $menu-dismiss-icon-color;
        width: 12px;
        height: 12px;
      }

      &:hover,
      &:focus {
        background-color: $menu-dismiss-bar-hover-color;

        .socrata-icon {
          color: $white;
        }
      }
    }
  }

  .menu-navigation {
    list-style-type: none;
    padding: 0;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 20px;

    li {
      .menu-list-item {
        text-align: left;
        display: block;
        width: 100%;
        padding: 20px 15px;
        font-weight: normal;
        color: $white;
        border-bottom: solid 1px $menu-item-border-color;
        border-left: 0;
        position: relative;
        transition: all 0.15s;
        cursor: pointer;
        text-decoration: none;

        &:before {
          content: "";
          display: block;
          position: absolute;
          width: 7px;
          background-color: $menu-item-border-color;
          left: 0;
          top: -1px;
          bottom: -1px;
        }

        &:hover,
        &:focus,
        &.active {
          background-color: $menu-item-background-color-active;
          color: $header-accent-color;

          &:before {
            background-color: $menu-item-border-color-active;
          }
        }

        &.active {
          .arrow .socrata-icon {
            transform: rotateZ(180deg);
          }

          & + .menu-list-item-content {
            max-height: 500px;

            > div {
              display: block;
            }
          }
        }

        span {
          display: inline-block;
          vertical-align: middle;
          margin-right: 25px;
        }

        .arrow {
          display: inline-block;
          float: right;
          margin-right: 0;

          .socrata-icon {
            height: 17px;
            width: 17px;
            margin-right: 0;
            transition: transform 0.15s ease-in-out;
          }
        }
      }

      .menu-list-item-content {
        background-color: $menu-item-background-color-active;
        border-left: solid 7px $menu-item-border-color;
        max-height: 0;
        overflow: hidden;

        > div {
          padding: 20px;
          border-bottom: solid 1px $menu-item-border-color;
          display: none;
        }

        .block-label {
          color: $white;
          line-height: 15px;
        }

        .text-input {
          width: 100%;
        }

        .text-area {
          width: 100%;
          height: 80px;
          max-width: 100%;
          padding: 8px;
          margin-bottom: 7px;
        }

        .btn {
          width: 100%;
        }

        form {
          margin-bottom: 0;
        }
      }

      &:first-child {
        .menu-list-item {
          border-top: solid 1px $menu-item-border-color;
        }
      }
    }
  }
}

@media (max-width: $small) {
  .side-menu {
    width: 100%;

    &.menu-anchor-right,
    &.menu-anchor-left {
      box-shadow: none;
    }
  }
}
